.footer {
    padding: 120px 0px 0px 0px;
    .container__footer {
        display: flex;
        align-items: flex-start;    
        justify-content: space-between;
        .left__block {
            display: flex;
            align-items: start;
            flex-direction: column;
            h2 {
                padding-bottom: 12px;
                font-family: 'Rubik';
                font-style: normal;
                font-weight: 500;
                font-size: 31.635px;
                line-height: 47px;
                letter-spacing: 1px;
                color: #40A3F8;
            }
            p {
                font-family: 'Rubik';
                font-style: normal;
                font-weight: 400;
                font-size: 14px;
                line-height: 180%;
                color: #646464;
                text-align: left;
                width: auto;
            }    
            span {
                padding: 16px 0px;
                font-family: 'Rubik';

                font-style: normal;
                font-weight: 400;
                font-size: 14px;
                line-height: 180%;
                color: #646464;
            }
            .icon {
                display: flex;
                justify-content: space-between;
                width: 180px;
            }
        }
        .right__block {
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            .block__nav {
                display: flex;
                flex-direction: column;
                align-items: start;
                padding-right: 114px;
                h6 {
                    font-family: 'Poppins';
                    font-style: normal;
                    font-weight: 700;
                    font-size: 16px;
                    line-height: 16px;
                    color: #323232;
                    padding-bottom: 24px;
                }
                ul {
                    display: flex;
                    flex-direction: column;
                    li:nth-child(2n) {
                        padding: 24px 0px;
                    }
                    a {
                        font-family: 'Poppins';
                        font-style: normal;
                        font-weight: 400;
                        font-size: 14px;
                        line-height: 14px;
                        color: #646464;
                        &:hover {
                            transition: all 0.6s ease;
                            color: #1f1f1f;
                            font-size: 15px;
                    }
                    
                }
            }
            }
            .sing__up {
                display: flex;
                align-items: start;
                flex-direction: column;
                width: 300px;
                h6 {
                    font-family: 'Rubik';
                    font-style: normal;
                    font-weight: 700;
                    font-size: 16px;
                    line-height: 16px;
                    color: #323232;
                }
             
                p {
                    width: 277px;
                    font-family: 'Rubik';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 180%;
                    color: #646464;
                    text-align: left;
                    padding: 19px 0px 20px 0px;
                }
                .email {
                    margin-bottom: 12px;
                    font-family: 'Rubik';
                    font-style: normal;
                    font-weight: 500;
                    font-size: 15px;
                    line-height: 16px;
                    color: rgba(110, 110, 110, 0.65);
                    width: 292px;
                    height: 40px;
                    background: #FFFFFF;
                    border: 1.1px solid #E1E2E4;
                    border-radius: 5px;
                    padding-left: 20px;
                }
                .email:hover {
                    background: #40A3F8;
                    transition: all 0.9s ease;
                    color: rgba(255, 255, 255, 0.65);
                }
                .button {
                    width: 292px;
                    height: 40px;
                    background: #40A3F8;
                    border: 1.1px solid #E1E2E4;
                    border-radius: 5px;
                    font-style: normal;
                    font-family: 'Rubik';
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 16px;
                    letter-spacing: 0.02em;
                    color: #FFFFFF;
                }
                .button:hover {
                    background: #238ce7;
                    transition: all 0.9s ease;
                    border: 1.1px solid #5ca8ff;
                }
            }
        }
    }
    .bottom__footer {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        padding-top: 60px;
        padding-bottom: 40px;
        span {
            font-family: 'Rubik';
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 14px;
            color: #646464;
        }
        .block__a {
            width: 240px;
            display: flex;
            justify-content: space-between;
            align-items: center;
