                font-family: 'Rubik';
                font-style: normal;
                font-weight: 400;
                font-size: 14px;
                line-height: 14px;
                color: #646464; 
                &:hover {
                    
                }
            }
        }
    }
    .container__footer-phone {
        display: none;
    }
}
