@import "zeroing";
@import "important";
@import "font-face";

.burger {
    display: none;
}
.burger-navigation {
    display: none;
}
.container__footer-phone {
    display: none;
}
.container__card-phone {
    display: none;
}
@import "./components/header";

h1 {
    font-family: 'Rubik';
     font-style: normal;
     font-weight: 900;
     font-size: 31.635px;
     line-height: 47px;   
     letter-spacing: 1px;
     color: #40A3F8;
}
.section {
    padding-top: 110px;
}
h2 {
    font-family: 'Rubik';
    font-style: normal;
    font-weight: 500;
    font-size: 42px;
    line-height: 70px;
    color: #3E3E3E;
    text-align: center;
}
p {
    width: 300px;
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 20px;
    text-align: center;
    color: rgba(0, 0, 0, 0.5);
}
.section:nth-child(1) {
    .container__block {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-top: 50px;
        .block {
            display: flex;
            flex-direction: column;
            align-items: center;
            .icon {
                width: 80px;
                height: 80px;
                position: relative;
                border-radius: 50%;
                img {
                    position: absolute;
                    left: 20%;
                    top: 25%;
                }
            }
            .icon:nth-child(1) {
                background: linear-gradient(180deg, rgba(0, 158, 255, 0.1) 0%, rgba(56, 175, 255, 0.1) 100%);
                mix-blend-mode: multiply;
                box-shadow: 0px 4px 22px rgba(0, 0, 0, 0.05);
            }
            .icon:nth-child(2) {
                background: radial-gradient(51.35% 53.44% at 48.82% 46.56%, rgba(255, 170, 6, 0.12) 0%, rgba(255, 180, 26, 0.12) 100%);    
                box-shadow: 0px 4px 22px rgba(0, 0, 0, 0.05);
            }
            .icon:nth-child(3) {
    
            }
            h3 {
                padding-top: 5px;
                font-family: 'Poppins';
                font-style: normal;
                font-weight: 500;
                font-size: 20px;
                line-height: 32px;
                color: #3D3D3F;
                padding-bottom: 20px;
            }
            p {
                width: 300px;
                text-align: center;
                font-size: 12px;
                line-height: 20px;
            }
        }
        .block:nth-child(2) {
            .icon {
                background: radial-gradient(51.35% 53.44% at 48.82% 46.56%, rgba(255, 170, 6, 0.12) 0%, rgba(255, 180, 26, 0.12) 100%);    
                box-shadow: 0px 4px 22px rgba(0, 0, 0, 0.05);
                img {
                    left: 24%;
                    top: 22%;
                }
            }
        }
    }
}
.section:nth-child(2) {
