                    }
                    .block__header {
                        display: flex;
                        flex-direction: column;
                        align-items: start;
                        h5 {

                        }
                        .header__text {
                            display: flex;
                            align-items: center;
                            span {
                                font-family: 'Poppins';
                                font-style: normal;
                                font-weight: 400;
                                font-size: 12px;
                                line-height: 18px;
                                color: rgba(61, 61, 63, 0.4);
                            }
                            .star {
                                padding-left: 5px;
                                display: flex;
                                align-items: center;
                                img {
                                    padding-right: 2px;
                                    height: 10px;
                                }
                                span {
                                    font-family: 'Poppins';
                                    font-style: normal;
                                    font-weight: 400;
