                        padding: 0px 10px;
                        p {
                            width: auto;
                        }
                        .p__text {
                            font-family: 'Poppins';
                            font-style: normal;
                            font-weight: 400;
                            font-size: 12px;
                            line-height: 22px;
                            display: flex;
                            align-items: center;
                            letter-spacing: 0.02em;
                            color: #A0A2B3;
                        }
                        .span__text {
                            font-family: 'Poppins';
                            font-style: normal;
                            font-weight: 500;
                            font-size: 12px;
                            line-height: 12px;
                            display: flex;
                            align-items: center;
                            color: #636481;
                        }
                    }
                }
                .block__footer {
                    padding-top: 14px;
                    display: flex;
                    align-items: center;
                    img {
                        padding-right: 10px;
                        position: static;
                    }
                    span {
                        font-family: 'Poppins';
                        font-style: normal;
                        font-weight: 400;
                        font-size: 12px;
                        line-height: 18px;
                        letter-spacing: 0.02em;
                        color: #3D3D3F;
                    }
                }
            }
            .block__woman {
                z-index: 10;
                top: -260px;
                left: 340px;
                position: relative;
                width: 230px;
                height: 80px;
                display: flex;
                background: #FFFFFF;
                box-shadow: 0px 4px 22px rgba(0, 0, 0, 0.05);
                border-radius: 6px;
                img {
                    top: 0px;
                    position: static;
                    padding: 16px 10px 16px 16px;
                }
                .lines {
                    position: static;
                    display: flex;
                    flex-direction: column;
                    align-items: start;
                    justify-content: center;
                    .line {
                        height: 4px;
                        margin: 5px 0px;
                        background: #E4F3FF;
                        border-radius: 5px;
                        &:nth-child(1) {
                            width: 135px;
                        }
                        &:nth-child(2) {
                            width: 110px;
                        }
                        &:nth-child(3) {
                            width: 85px;
                        }
                    }
                }
            }
        }
        .block__text {
            padding-left: 120px;
            display: flex;
            align-items: start;
            flex-direction: column;
            p {
                font-size: 18px;
                text-align: left;
                width: 100%;
                padding-top: 20px;
                padding-bottom: 30px;
                line-height: 30px;
            }
            h2 {
                line-height: 50px;
                color: #3E3E3E;
                text-align: start;
            }
            .text__container {
                display: flex;
                align-items: start;
                flex-direction: column;
                padding-bottom: 24px;
                .strike {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    padding-bottom: 15px;
                    span {
                        font-family: 'Poppins';
                        font-style: normal;
                        font-weight: 400;
                        font-size: 16px;
                        line-height: 30px;
                        letter-spacing: 0.02em;
                        color: #3D3D3F;
                        padding-left: 18px;
                    }
                }
            }
            .btn {
                background: #40A3F8;
                border-radius: 5px;
                padding: 10px 18px;
                display: flex;
                margin-left: 0px;
                transition: all 0.7s ease;
                img {
                    padding-left: 10px;
                }
                &:hover {
                    transition: all 0.5s ease;
                    color: #ffffff;
                    background: #3191ff;
                    box-shadow: 3px 4px 22px rgba(255, 255, 255, 0.05);          
                }
            }
        }
    }
}
.section:nth-child(3) {
    display: flex;
    flex-direction: column;
    align-items: center;
    p {
        margin: 0 auto;
        padding-top: 24px;
        width: 510px;
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 30px;
        text-align: center;
        letter-spacing: 0.02em;
        color: rgba(61, 61, 63, 0.7);
    }
    .background__right {
        width: 438px;
        height: 494px;
        position: absolute;
        z-index: 0;
        right: 30px;
        background: linear-gradient(90.26deg, rgba(170, 128, 255, 0.7) 0.21%, rgba(160, 113, 255, 0.455) 97.38%);
        filter: blur(444px);
        transform: rotate(90deg);
    }
    .background__left {
        position: absolute;
        width: 438px;
        height: 494px;
        left: 10px;
        z-index: 0;
        background: #99D4ED;
        filter: blur(444px);
        transform: rotate(90deg);
    }
    .container__card {
        padding-top: 75px;
        .cards.active {
            display: block;
            display: flex;
            justify-content: space-between;
            align-items: center;
            opacity: 1;
            transition: all 0.6s ease;
            .card {
                display: block;
                width: 350px;
                height: 320px;
                left: 135px;
                top: 2277px;
                background: linear-gradient(138.88deg, rgba(254, 254, 254, 0) -2.44%, #F9F9F9 -2.43%, rgba(254, 254, 254, 0.51) 98.26%);
                filter: drop-shadow(0px 4px 120px rgba(0, 0, 0, 0.05));
                border-radius: 10px;
                .card__section {
                    padding: 60px 30px 30px 50px;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    h5 {
                        padding-top: 20px;
                    }
                    p {
                        width: 224px;
                        font-family: 'Poppins';
                        font-style: normal;
                        font-weight: 400;
                        font-size: 14px;
                        line-height: 22px;
                        text-align: center;
                        color: #999999;
                    }
                    .line {
                        width: 350px;
                        height: 18px;
                        background: #40A3F8;
                        opacity: 0.5;
                        box-shadow: 0px 4px 120px rgba(0, 0, 0, 0.05);
                        border-radius: 0px 0px 10px 10px;
                        position: relative;
                        left: -10px;
                        bottom: -25px;
                    }
                }
            }
        }
        .cards {
            opacity: 0;
            transition: all 0.6s ease;
            .card {
                display: none;
            }
        }
    }
    .arrow__dot {
        padding-top: 80px;
        display: flex;
        justify-content: center;
        align-items: center;
        .dot {
            width: 18px;
            height: 18px;      
            background: #DBE8EE;
            cursor: pointer;
            border-radius: 50%;
            margin: 0px 10px;
            opacity: 1;
            transition: all 0.7s ease;
        }
        .dot.active {
            width: 60px;
            height: 15px;
            background: #40A3F8;
            border-radius: 6px;
            cursor: pointer;
            border-radius: 8px;
            opacity: 1;
            transition: all 0.4s ease;
        }
    }
}
