    }
    .swiper-container {
        padding-top: 70px;
        display: flex;
        justify-content: space-between;
        overflow: hidden;
        position: relative;
        .container__slider {
            display: flex;
            justify-content: space-between;
            .card {
                border-radius: 5px;
                max-width: 360px;
                height: 347px;
                background: linear-gradient(132.1deg, rgba(255, 255, 255, 0.88) 0.65%, #FAFAFA 103.13%);
                box-shadow: 0px 4px 22px rgba(0, 0, 0, 0.05);
                .block__card {
                    position: relative;
                    padding: 22px;
                    .img {
                        position: absolute;
                        left: 150px;
                        border-radius: 50%;
                    }
                    .quote {
                        position: absolute;
                        top: -20px;
                        left: 280px;
                    }
                    .block__star {
                        position: absolute;
                        top: 90px;
                        left: 250px;
                    }
                    h5 {
                        position: absolute;
                        top: 80px;
                        left: 30px
                    }
                    span {
                        position: absolute;
                        top: 110px;
                        left: 30px;

                        font-size: 14px;
                        line-height: 25px;
                        background: linear-gradient(90.26deg, #47C2FF 0.21%, rgba(99, 203, 254, 0.65) 97.38%);
                        -webkit-background-clip: text;
                        -webkit-text-fill-color: transparent;
                        background-clip: text;
                    }
                    p {
                        position: absolute;
                        top: 160px;
                        left: 30px;
                        text-align: left;

                        font-family: 'Poppins';
                        font-style: normal;
                        font-weight: 400;
                        font-size: 16px;
                        line-height: 25px;
                        color: #454D5D;
                    }
                }
                &:nth-child(n + 2) {
                    margin: 0px 15px;
                }
            }
        }
    }
    .arrow__button {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        position: relative;
        bottom: 180px;
        .swiper-button-prev{
            position: absolute;
            width: 33.45px;
            height: 32px;
            background: #40A3F8;
            box-shadow: 0px 4px 22px rgba(0, 0, 0, 0.05);
            border-radius: 5px;
            left: -100px;
            cursor: pointer;
            transition: all 0.5s ease-in-out;
            .svg  {
                color: #FFFFFF;
                transform: rotate(135deg);
                -webkit-transform: rotate(135deg);
                border: solid rgb(255, 255, 255);
                border-width: 0 3px 3px 0;
                display: inline-block;
                padding: 6px;
                position: absolute;
                top: 9px;
                left: 11px;
                border-radius: 1px;
            }  
            &:hover {
                transition: all 0.4s ease-in-out;
                background: linear-gradient(133.16deg, rgba(154, 208, 255, 0.1065) 0%, rgba(111, 188, 255, 0.1005) 112.21%);
                box-shadow: 0px 4px 40px rgba(0, 0, 0, 0.07);
                border-radius: 5px;
                .svg {
                    transition: all 0.4s ease-in-out;
                    border: solid #40A3F8;  ;
                    border-width: 0 3px 3px 0;
                }
            } 
            }
        .swiper-button-next {
            cursor: pointer;
            position: absolute;
            width: 33.45px;
            height: 32px;
            background: #40A3F8;
            box-shadow: 0px 4px 22px rgba(0, 0, 0, 0.05);
            border-radius: 5px;
            right: -100px;
            transition: all 0.5s ease-in-out;
            .svg  {
                color: #FFFFFF;
                transform: rotate(-45deg);
                -webkit-transform: rotate(-45deg);
                border: solid rgb(255, 255, 255);
                border-width: 0 3px 3px 0;
                display: inline-block;
                padding: 6px;
                position: absolute;
                top: 9px;
                left: 7px;
                border-radius: 1px;
                }
            &:hover {
                transition: all 0.4s ease-in-out;
                background: linear-gradient(133.16deg, rgba(154, 208, 255, 0.1065) 0%, rgba(111, 188, 255, 0.1005) 112.21%);
                box-shadow: 0px 4px 40px rgba(0, 0, 0, 0.07);
                border-radius: 5px;
                .svg {
                    transition: all 0.4s ease-in-out;
                    border: solid #40A3F8;  ;
                    border-width: 0 3px 3px 0;
                }
            } 
        }
    }
}

