        background-color: transparent;
        cursor: pointer;
        .line:nth-child(1) {
            position: absolute;
            height: 24px;
            width: 3px;
            border-radius: 3px;

            right: 0px;
            transform: rotate(45deg);
            background-color: #000000;
        }
        .line:nth-child(2) {
            position: absolute;
            height: 24px;
            width: 3px;
            border-radius: 3px;
            right: 0px;
            transform: rotate(-45deg);
            background-color: #000000;
        }
    }
    .modal-overlay--visible {
        opacity: 1;
        visibility: visible;
        transition: all 0.3s ease-in-out;
    }
    .modal-overlay--visible .modal {
        display: block;
    }
    .modal--visible {
        display: block;
    }
}

