@import "./../mixins/fonts";

.header {
    padding-top: 15px;
    background: #edf9ff; /* Old browsers */
    background: -moz-linear-gradient(left,  #edf9ff 0%, #c5dcec 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(left,  #edf9ff 0%,#cbdeec 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to right,  #edf9ff 0%,#cde2f1 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#edf9ff', endColorstr='#b3cde0',GradientType=1 ); /* IE6-9 */
    height: 880px;
}
.navigation {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .h2 {
        font-family: 'Rubik';
        font-style: normal;
        font-weight: 500;
        font-size: 31.635px;
        line-height: 47px;
        letter-spacing: 1px;
        color: #40A3F8;
    }
    .block__navigation {
        display: flex;
        align-items: center;
        ul {
            display: flex;
            align-items: center;
            justify-content: space-between;
            li {
                padding: 0px 25px;
                a {
                    font-family: 'Poppins';
                    font-style: normal;
                    font-weight: 600;
                    font-size: 16px;
                    line-height: 24px;
                    color: #6E5F5F;
                    mix-blend-mode: normal;
                    opacity: 0.3;
                    transition: all 0.5s ease;
                }
                a:hover {
                    transition: all 0.6s ease;
                    color: #020202;
                    opacity: 0.7;
                    font-size: 17px;
                }
            }
        }
    }
}
.header__container {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding-top: 240px;
    .container__title {
        display: flex;
        align-items: start;
        flex-direction: column;
        h1 {
            width: 532px;
            font-family: 'Poppins';
            font-style: normal;
            font-weight: 700;
            font-size: 48px;
            line-height: 70px;
            color: #3E3E3E;
            span {
                color: #40A3F8;
            }
        }
        p {
            padding-top: 30px;
            font-family: 'Poppins';
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 30px;
            color: rgba(117, 117, 117, 0.46);
            width: 460px;
            text-align: left;
            padding-bottom: 40px;
        }
        .btn__header {
            font-family: 'Poppins';
            font-style: normal;
            font-weight: 500;
            font-size: 22px;
            line-height: 22px;
            padding: 12px 33px;
            background: #40A3F8;
            border-radius: 5px;
            color: #FFFFFF;
            margin-left: 0px;
            transition: all 0.7s ease;
            display: flex;
            align-items: center;
            span {
                padding-right: 15px;
            }
            &:hover {
                background: #257ecc;
                transition: all 0.8s ease;
            }
        }
        
       
    }
    .container__img {
        .img__block {
            position: relative;
            img:nth-child(1) {
                position: absolute;
                top: -74px;
                right: 20px;
                z-index: 100;
            }
            .img-1 {
                position: absolute;
                top: -100px;
                right: -40px;
                z-index: 1;
            }
            .block__icons-one {
                position: relative;
                right: 580px;
                top: 20px;
                img {
                    position: absolute;
                    top: 0px;
                    left: 0px;
                }
            }
            .block__icons-two {
                position: relative;
                right: 600px;
                top: 210px;
                img {
                    position: absolute;
                    top: 0px;
                    left: 0px;
                }
            }
            .block__icons-three {
                position: relative;
                right: 150px;
                top: 260px;
                img {
                    position: absolute;
                    top: 0px;
                    left: 0px;
                }
            }
            .block__icons-four {
                position: relative;
                right: 60px;
                img {
                    position: absolute;
                    top: 0px;
                    left: 0px;
                }
            }
            .block__icons-five {
                position: absolute;
                width: 215px;
                height: 65px;
                background-color: #fff;
                border-radius: 10px;
                display: flex;
                align-items: center;
                justify-content: space-between;
                z-index: 120;

                right: 380px;
                top: 400px;
                img {
                    top: 11px;
                    left: 22px;
                }
                .block {
                    display: flex;
                    align-items: start;
                    flex-direction: column;
                    position: absolute;
                    left: 80px;
                    top: 15px;
                    .block__star {
                        display: flex;
                        justify-content: space-between;
                        width: 100px;
                        .star {
                            position: static;
                        }
                    }
                    .line__one {
                        position: absolute;
                        top: 24px;
                        width: 120.96px;
                        height: 3.76px;
                        background: #E4F3FF;
                    }
                    .line__two {
                        position: absolute;
                        top: 33px;
                        width: 110.96px;
                        height: 3.76px;
                        background: #E4F3FF;
                    }
                }
            }
        }
    }
}
