@media screen and(max-width: 1060px) {
    .burger {
        display: block;
        z-index: 200;
        opacity: 1;
        transition: all 0.6s ease;
        position: relative;
        width: 30px;
        height: 20px;
        background-color: transparent;
        cursor: pointer;
        .line:nth-child(3) {
            content: '';
            position: absolute;
            top: 0%;
            left: 0%;
            border-radius: 2px;
            width: 30px;
            height: 3px;
            background-color: #FFFFFF;
            transition: all 0.3s ease;
        }
        .line:nth-child(2) {
            position: absolute;
            top: 50%;
            left: 0%;
            border-radius: 2px;
            width: 30px;
            height: 3px;
            background-color: #FFFFFF;
            transition: all 0.3s ease;
        }
        .line:nth-child(1) {
            content: '';
            position: absolute;
            top: 100%;
            left: 0%;
            border-radius: 2px;
            width: 30px;
            height: 3px;
            background-color: #FFFFFF;
            transition: all 0.3s ease;
        }
    }
    .burger.active {
        display: block;
        transition: all 0.5s ease;
        overflow: hidden;
        width: 40px;
        height: 30px;
        left: 15px;
        .line:nth-child(3) {
            top: 50%;
            transition: all 0.4s ease;
            transform: rotate(45deg);
        }
        .line:nth-child(2) {
            display: none;
            opacity: 0;
        }
        .line:nth-child(1) {
            top: 50%;
            transition: all 0.4s ease;
            transform: rotate(-45deg);
        }
    }
    .stop-scroll {
        overflow: hidden;
    }

    
    
    .disable-scroll {
        position: relative;
        overflow: hidden;
        height: 100vh;
        position: fixed;
        // left: 0;
        // top: 0;
        // width: 100%;
    }


    .header {
        overflow: hidden;
    }
    .navigation {
        position: relative;
    }
    .burger-navigation {
        width: 380px;
        height: 771px;
        background: rgba(0, 0, 0, 0.4);
        
        transition: all 0.5s ease-in-out;
        display: block;
        position: absolute;
        top: -15px;
        right: -800px;
        display: flex;
        flex-direction: column;
        align-items: start;
        ul {
            display: flex;
            align-items: flex-end;
            flex-direction: column;
            position: absolute;
            top: 80px;
            left: 150px;
            li {
                padding: 10px 0px;
                a {
                    margin: 14px 0px;
                    font-family: 'Poppins';
                    font-style: normal;
                    font-weight: 600;
                    font-size: 16px;
                    line-height: 24px;
                    color: #ffffff;
                    mix-blend-mode: normal;
                    opacity: 1;
                    transition: all 0.5s ease;
                }
                a:hover {
                    transition: all 0.6s ease;
                    color: #020202;
                    opacity: 0.7;
                    font-size: 17px;
                }
            }
        }
        .btn {
            position: absolute;
            top: 320px;
            left: 65px;
            
        }
    }
    .burger-navigation.active {
        right: -160px;
        top: -15px;
        transition: all 0.8s ease-in-out;
        z-index: 110;
    }


    .navigation .block__navigation {
        display: none;
    }
    .header__container {
        padding-top: 60px;
    }
    .header {
        height: 771px;
    }
    .header__container .container__img .img__block img:nth-child(1) {
        position: absolute;
        top: 236px;
        right: 130px;
        z-index: 100;
    }
    .header__container .container__img .img__block .img-1 {
